<template>
  <!-- 左侧导航栏 -->
  <!-- @change="searchAction()"  :autofocus="isfocus"  @focus="handleSearch()"   -->
  <div id="resultList">
    <div class="result-search">
      <el-input
        ref="inlineInput"
        type="text"
        v-model="search"
        prefix-icon="el-icon-search"
        clearable
        @clear="clearList()"
        @change="searchAction()"
        @focus="handleSearch()"
        @blur="clear()"
      >
      </el-input>
    </div>
    <div class="result-show">
      <!-- <el-scrollbar ref="scrollList">
         v-infinite-scroll="load"
          :infinite-scroll-disabled="disabled"
          :infinite-scroll-immediate="false"
          :infinite-scroll-delay="500" -->
      <ul class="result-show-box" @click="handleClick($event)">
        <li
          class="result-show-box-item"
          v-for="(item, index) in resultList"
          :class="selectName == item.dataSetName ? 'is-active' : ''"
          :key="item + index"
          :data-index="index"
        >
          {{ item.dataSetName }}
        </li>
        <!-- <li class="result-show-box-loading" v-if="loading">加载中...</li>
        <li class="result-show-box-noMore" v-if="disabled && !loading">
          没有更多了
        </li> -->
      </ul>
      <div class="pages">
        <el-pagination
          small
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :layout="layout"
          @current-change="handleCurrentChange"
          :total="total"
        >
        </el-pagination>
      </div>
      <!-- </el-scrollbar> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1, // 当前页码
      total: null, //总条数
      pageSize: 18,
      layout: "prev, pager, next", //控制 分页组件的布局顺序
      // 搜索功能
      search: "",
      pageNum: 1,
      // isfocus: true,
      searchSwitch: false, //搜索模式开关
      //搜索返回的数据列表
      searchList: {
        searchData: [],
        totalPages: "",
        currentPage: "",
        pageNum: 1,
        disabled: false,
        isNext: "",
        loading: false,
        // length: "",
      },
      // 二级菜单列表信息
      resultList: [], //全部信息
      // length: "", //列表的长度
      // disabled: false, //停止滚动加载标志
      // loading: false, //加载中
      // isNext: "", //分页加载的标志
      // totalPages: "",
      // currentPage: "",
      // pageNum: 1, //分页加载的页数

      getProjectParams: {
        pageNum: 1,
        pageSize: 20,
        isNext: "",
      },

      // 缓存数据
      cacheCurrentData: {
        curIndex: 0, //搜索时缓存当前索引
        dataSetName: [], //搜索时 缓存当前项目
        navList: [], //搜索时 缓存navList
        disabled: false,
        getProjectParams: "",
      },
      dataType: "",
      // 二级选中菜单名称
      selectName: "",
      // 选中的下标
      curIndex: 0,
    };
  },
  computed: {
    // 选中的二级菜单
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    // 选中一级类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // 列表展示数据
    resultLists() {
      return this.resultList;
    },
    //删除当前选中
    isDelectVuex() {
      return this.$store.state.ItemDetails.isDelect;
    },
    //新建的数据集参数
    addFormVuex() {
      return this.$store.state.ItemDetails.addForm;
    },
  },
  watch: {
    // 监听选中的数据集类型
    currentType: {
      handler(newValue) {
        this.$store.commit("ItemDetails/currentType", newValue);
        // this.currentType = newValue;
        console.log(this.currentType);
        this.pageNum = 1;
        // this.curIndex = 0;
        this.resultList = [];
        this.getProjectParams.pageNum = 1;
        // this.disabled = false;
        this.getreslutList();
      },
    },
    //监听选中数据集
    // selectName: {
    //   handler(newValue) {
    //     // if (this.dataType != "") {
    //     console.log(newValue);
    //     let itemDate = newValue;
    //     let itemIndex = this.curIndex;
    //     this.$store.commit("ItemDetails/currentDate", {
    //       itemDate,
    //       itemIndex,
    //     });
    //   },
    //   // },
    // },
    //新建
    addFormVuex: {
      handler(newValue) {
        console.log("新建##");
        if (this.search != "") {
          this.search = "";
          this.clear();
        }

        // console.log(newValue);
        let item = {
          dataSetName: newValue.dataSetName,
          desc: newValue.desc,
          id: newValue.id,
          dataType: newValue.dataType,
        };
        this.resultList.splice(0, 0, item);
        this.curIndex = 0;
        this.selectName = this.resultList[this.curIndex].dataSetName;
        this.$store.commit("ItemDetails/currentDate", {
          itemDate: this.selectName,
          itemIndex: this.curIndex,
        });

        // this.length += 1;
        // this.curIndex += 1;
      },
      deep: true,
    },
    //删除
    isDelectVuex: {
      handler() {
        // if (this.dataType == this.currentType) {
        // if (newVal == true) {
        console.log("删除******************");
        console.log(this.$store.state.ItemDetails.itemIndex);
        this.curIndex = this.$store.state.ItemDetails.itemIndex;
        this.resultList.splice(this.curIndex, 1);
        let length = this.resultList.length;
        if (length > 0 && this.curIndex < length) {
          console.log(this.resultList[this.curIndex]);
          this.selectName = this.resultList[this.curIndex].dataSetName;
          this.$store.commit("ItemDetails/currentDate", {
            itemDate: this.selectName,
            itemIndex: this.curIndex,
          });
        } else {
          console.log(this.resultList[this.curIndex]);
          this.curIndex = length - 1;
          this.selectName = this.resultList[this.curIndex].dataSetName;
          this.$store.commit("ItemDetails/currentDate", {
            itemDate: this.selectName,
            itemIndex: this.curIndex,
          });
        }
        //删除项目时如果当前项目列表长度小于20,会获取新的数据添加到列表里面,确保撑出滚动条
        if (this.resultList.length < 20) {
          this.load();
        }
        // }
      },
      deep: true,
    },
    search: {
      handler(newVal) {
        console.log("搜索值改变了" + newVal);
        if (newVal != "") {
          this.disabled = false;
          this.getProjectParams.pageNum = 1;
        }
      },
    },
  },

  methods: {
    handleCurrentChange(page) {
      console.log(`当前页: ${page}`);
      this.currentPage = page;
      // this.getTime();
      if (this.search !== "") {
        this.searchAction();
      } else this.getreslutList();
    },
    //滚动触发加载数据
    load() {
      console.log("******************load****************");
      console.log(this.searchSwitch, this.isNext, this.searchList.isNext);
      if (!this.searchSwitch) {
        this.loadresult();
      } else {
        this.loadSearch();
      }
    },
    getreslutList() {
      // this.resultList = "";
      console.log("开始获取二级菜单");
      this.$api
        .resultListV2({
          pageNum: this.currentPage,
          pageSize: 18,
          dataType: this.currentType,
        })
        .then((res) => {
          console.log(res);
          this.resultList = res.mapMsg;
          this.currentPage = res.currentPage;
          this.total = res.totalPages * 18;
          if (res.totalPages < 1) {
            this.$store.commit("ItemDetails/targetCheck", this.resultList);
          }

          this.length = this.resultList.length;
          this.isNext = res.isNext;
          this.getProjectParams.isNext = res.isNext;
          this.disabled = !res.isNext;
          this.dataType = res.dataType;
          console.log(this.selectName);
          if (this.selectName == "") {
            this.selectName = this.resultList[0].dataSetName;
            this.curIndex = 0;
            this.$store.commit("ItemDetails/currentDate", {
              itemDate: res.mapMsg[0].dataSetName,
              itemIndex: 0,
            });
          } else if (this.currentType == this.dataType) {
            this.selectName = this.resultList[this.curIndex].dataSetName;
            this.$store.commit("ItemDetails/currentDate", {
              itemDate: this.resultList[this.curIndex].dataSetName,
              itemIndex: this.curIndex,
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 滚动加载 二级菜单数据
    loadresult() {
      console.log(this.disabled);
      console.log("滚动 获取二级菜单");

      if (!this.disabled) {
        this.loading = true;
        // this.disabled = true;
        // this.pageNum += 1;
        this.getProjectParams.pageNum += 1;
        let pageNum = this.getProjectParams.pageNum;
        let pageSize = this.getProjectParams.pageSize;
        this.$api
          .resultListV2({
            pageNum: pageNum,
            pageSize: pageSize,
            dataType: this.currentType,
          })
          .then((res) => {
            console.log(res.mapMsg);
            this.disabled = !res.isNext;
            this.getProjectParams.isNext = res.isNext;
            this.isNext = res.isNext;
            this.resultList.push(...res.mapMsg);
            console.log(this.resultList);
            this.$store.commit("ItemDetails/targetCheck", this.resultList);

            this.loading = false;
          });
      }
    },
    // 滚动加载 搜索数据
    loadSearch() {
      console.log(this.disabled);
      console.log("滚动#  搜索列表");
      if (!this.disabled) {
        let inputValue = this.search;
        this.getProjectParams.pageNum += 1;
        let pageNum = this.getProjectParams.pageNum;
        let pageSize = this.getProjectParams.pageSize;
        this.$api
          .searchData({
            keyword: inputValue,
            pageSize: pageSize,
            pageNum: pageNum,
            dataType: this.currentType,
          })
          .then((res) => {
            console.log(res);
            if (res.code == 200) {
              this.resultList.push(...res.mapMsg);
              this.searchList.isNext = res.isNext;
              this.disabled = !res.isNext;
              this.searchList.searchData.push(...res.mapMsg);
            } else if (res.code == 401) {
              this.resultList = "";
            }
          })
          .catch((res) => {
            console.log(res);
          });
      }

      // }
    },
    // enter键入搜索功能
    searchAction() {
      // if (this.search != "") {
      this.currentPage = 1;
      console.log(this.search);
      if (this.search !== "") {
        this.searchSwitch = true;
        this.resultList = "";
        this.searchList.searchData = "";
        let inputValue = this.search;
        this.$api
          .searchData({
            keyword: inputValue,
            pageSize: 18,
            pageNum: this.currentPage,
            dataType: this.currentType,
          })
          .then((res) => {
            console.log(res);
            if (res.code == 200) {
              this.resultList = "";
              this.resultList = res.mapMsg;
              this.currentPage = res.currentPage;
              this.total = res.totalPages * 18;
              this.isNext = res.isNext;
              this.disabled = !res.isNext;
              this.searchList.currentPage = res.currentPage;
              // this.searchList.searchData = res.mapMsg;
              this.searchList.totalPages = res.totalPages;
              this.searchList.currentPage = res.currentPage;
              this.searchList.isNext = res.isNext;
            } else {
              this.$message({
                type: "error",
                message: "没有匹配结果",
                showClose: true,
              });
            }
          })
          .catch((res) => {
            console.log(res);
          });
      }
    },
    // 输入框聚焦触发
    handleSearch() {
      console.log("开始搜索");
      // this.search = "";
      // this.searchList.searchData = "";
      if (this.search == "") {
        this.cacheCurrentData.navList = this.resultList;
        // this.cacheCurrentData.navList = [...this.resultList];
        // this.cacheCurrentData.curIndex = this.curIndex;
        this.cacheCurrentData.dataSetName = this.selectName;
        this.cacheCurrentData.disabled = this.disabled;
        this.cacheCurrentData.getProjectParams = { ...this.getProjectParams };
        //将公共变量初始化
        this.getProjectParams = {
          pageNum: 1,
          pageSize: 20,
          dataType: this.currentType,
          isNext: this.getProjectParams.isNext,
        };
        console.log("缓存数据" + this.cacheCurrentData.navList);
        this.searchSwitch = true;
        // this.resultList = "";
        // this.getreslutList();
        // console.log(this.$store.state.ItemDetails.menuList);
      } else {
        this.disabled = false;
        this.searchSwitch = true;
      }

      // this.loading = false;
      // this.searchList.disabled = false;
    },
    // 点击叉号清除搜索功能
    clearList() {
      console.log("清除搜索列表");
      this.searchSwitch = false;
      // this.getProjectParams.pageNum = 1;
      this.$refs["inlineInput"].blur();
      // this.clear();
    },
    // 输入框失去焦点
    clear() {
      console.log("失去焦点");
      if (this.search == "") {
        this.searchSwitch = false;
        this.resultList = "";
        this.resultList = [...this.cacheCurrentData.navList];
        this.selectName = this.cacheCurrentData.dataSetName;
        // this.curIndex = this.cacheCurrentData.curIndex;
        this.disabled = this.cacheCurrentData.disabled;
        this.getProjectParams = { ...this.cacheCurrentData.getProjectParams };
        console.log(this.getProjectParams);
      }
    },
    // 点击选中
    handleClick(e) {
      // console.log(e);
      // console.log(e.target.innerText);
      // console.log(e.target.className);
      if (/result-show-box-item/.test(e.target.className)) {
        this.selectName = e.target.innerText;
        this.curIndex = e.target.dataset.index;
        let itemDate = e.target.innerText;
        let itemIndex = e.target.dataset.index;
        console.log(e.target.dataset.index);
        this.$store.commit("ItemDetails/currentDate", { itemDate, itemIndex });
        let tagData = {
          itemName: itemDate,
          routeName: this.$route.name,
          pageIndex: "",
          icon: "",
        };
        console.log(tagData);
        this.$store.commit("NavigationPage/setCompTagTags", tagData);
      }
    },
  },
  created() {
    // this.getreslutList();
  },
  mounted() {
    // this.getreslutList();
  },
  activated() {
    this.getreslutList();
    // this.getsessionList();
  },
};
</script>

<style lang="scss" scoped>
#resultList {
  background-color: #343b6f;
  .result-search {
    // padding: 0px;
    height: 50px;
    width: 100%;
    line-height: 50px;
    text-align: center;
    background-color: #343b6f;
    .el-input {
      height: 30px;
      width: 80%;
    }
    /deep/ .el-input__inner {
      height: 30px;
      width: 100%;
      border-radius: 20px;
      background-color: #4c537f;
    }
  }
  .result-show {
    height: calc(100vh - 100px);
    width: 100%;
    // white-space: nowrap;
    &-box {
      width: 100%;
      // height: 100%;
      height: 85%;
      margin-bottom: 20px;
      color: #fff;
      overflow: hidden;
      &-item {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        // background: aliceblue;
        background-color: #343b6f;
        line-height: 40px;
        text-align: center;
        // margin: 10px 0;
      }
      &-item:hover {
        // background-color: rgb(150, 205, 241);
        background-color: rgb(54, 45, 45);
      }
      .is-active {
        background-color: #3c5880;
      }
      &-loading {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
        background-color: rgb(35, 173, 17);
      }
      &-noMore {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 40px;
        width: 100%;
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
        background-color: rgb(159, 175, 17);
      }
    }
    .pages {
      text-align: center;
      /deep/ .el-pagination span {
        background: #4d4f62;
        // border: 1px solid #ffbb46;
        color: #ffffff;
        // padding: 0 5px;
        // line-height: 2;
        // border-radius: 4px;
      }
      /deep/ .el-pagination.is-background.el-pagination--small .el-pager li {
        margin: 0 1px;
      }
      /deep/ .el-pagination.is-background .el-pager li:not(.disabled) {
        background-color: #4d4f62;
        color: #fff;
      }

      /deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: green; // 进行修改选中项背景和字体
        color: #fff;
      }
      /deep/ .el-pagination.is-background .btn-prev {
        background-color: #4d4f62;
        color: #fff;
      }
      /deep/ .el-pagination.is-background .btn-next {
        background-color: #4d4f62;
        color: #fff;
      }
    }
    .el-scrollbar {
      height: 100%;
    }
  }
}
</style>
